/*Card options*/

.card-collapsed {
    > :not(.card-header):not(.card-status) {
        display: none;
    }
    .card-options-collapse i:before {
        content: '\e92d';
    }
}

.card-fullscreen {
    .card-options-fullscreen i:before {
        content: '\e992';
    }
    .card-options-remove {
        display: none;
    }
}


/*Card maps*/

.card-map {
    height: 15rem;
    background: #e9ecef;
}

.card-map-placeholder {
    background: no-repeat center;
}


/**Card tabs*/

.card-tabs {
    display: -ms-flexbox;
    display: flex;
}

.card-tabs-bottom .card-tabs-item {
    border: 0;
    &.active {
        border-top-color: $white;
    }
}

.card-tabs-item {
    -ms-flex: 1 1 auto;
    flex: 1 1 auto;
    display: block;
    padding: 1.4rem 1.5rem;
    color: inherit;
    overflow: hidden;
}

a.card-tabs-item {
    background: #fafbfc;
    &:hover {
        text-decoration: none;
        color: inherit;
    }
    &:focus {
        z-index: 1;
    }
    &.active {
        background: $white;
        border-bottom-color: $white;
    }
}


/**Card status*/

.card-status {
    position: absolute;
    top: -1px;
    left: 0px;
    right: 0px;
    height: 3px;
    border-radius: 7px 7px 0 0;
}

.card-status-left {
    right: auto;
    bottom: 0;
    height: auto;
    width: 3px;
    border-radius: 3px 0 0 3px;
}


/**Card icon*/

.card-icon {
    width: 3rem;
    font-size: 2.5rem;
    line-height: 3rem;
    text-align: center;
}


/**Card fullscreen*/

.card-fullscreen {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
    margin: 0;
}


