            display: inline-block;
            padding: 4px 10px;
            border-radius: 15px;
        }
    }
    p {
        color: #262626;
    }
}

.card-counter {
    padding: 20px 10px;
    background-color: $white;
    height: 100px;
    border-radius: 7px;
    transition: .3s linear all;
    margin-bottom: 1.5rem;
    &:hover {
        transition: .3s linear all;
    }
    &.primary {
        color: #FFF;
    }
    &.secondary {
        background: linear-gradient(to bottom right, #fbc434 0%, #f66b4e 100%) !important;
        color: #FFF;
    }
    &.success {
        background: linear-gradient(to bottom right, #62fb62, #21a544) !important;
        color: #FFF;
    }
    &.info {
        background: linear-gradient(to bottom right, #00f2fe 0%, #1e63c3 100%) !important;
        color: #FFF;
    }
    i {
        font-size: 4rem;
        opacity: 0.3;
    }
    .count-numbers {
        position: absolute;
        right: 35px;
        top: 10px;
        font-size: 32px;
        display: block;
    }
    .count-name {
        position: absolute;
        right: 35px;
        top: 65px;
        text-transform: capitalize;
        opacity: 0.5;
        display: block;
        font-size: 18px;
    }
}

