        background: $black-5;
        width: 100%;
        height: 100%;
        right: 0;
        top: 0;
    }
}

.card-blog-overlay1:before,
.card-blog-overlay2:before {
    content: '';
    display: block;
    position: absolute;
    background: $black-5;
    width: 100%;
    height: 100%;
    right: 0;
    top: 0;
}

.card-blog-overlay1 {
    background: url('../images/media/thumbnails/thumb3.jpg');
    background-size: cover;
}

.card-blog-overlay2 {
    background: url('../images/media/22.jpg');
    background-size: cover;
}


/*------ Default Card Values -------*/

.card {
    position: relative;
    margin-bottom: 1.5rem;
    width: 100%;
    .card {
        border: 1px solid #e9edf4;
    }
}

@media print {
    .card {
        box-shadow: none;
        border: none;
    }
}

.card-body {
    +.card-body {
        border-top: 1px solid #e9edf4;
    }
    > :last-child {
        margin-bottom: 0;
    }
}

@media print {
    .card-body {
        padding: 0;
    }
}

.card-body-scrollable {
    overflow: auto;
}

.card-footer,
.card-bottom {
    padding: 1rem 1.5rem;
    background: none;
}

@media print {
    .card-header {
        display: none;
    }
}

.card-title {
    font-size: 1rem;
    font-weight: 500;
    margin-bottom: 1.5rem;
}

.reg {
    text-align: center;
    font-size: 50px;
    color: #2e1170;
    float: right;
}

.card-title {
    a {
        color: inherit;
    }
    &:only-child {
        margin-bottom: 0;
    }
    small {
        color: #9aa0ac;
        font-size: 0.875rem;
        display: block;
        margin: -.75rem 0 1rem;
        line-height: 1.1;
        font-weight: 400;
    }
}

.card-subtitle {
    color: #9aa0ac;
    font-size: 0.875rem;
    display: block;
    margin: -.75rem 0 1rem;
    line-height: 1.1;
    font-weight: 400;
}

.card-table {
    margin-bottom: 0;
    tr {
        &:first-child {
            td,
            th {
                border-top: 0;
            }
        }
        td:first-child,
        th:first-child {
            padding-left: 1.5rem;
        }
        td:last-child,
        th:last-child {
            padding-right: 1.5rem;
        }
    }
}

.card-body+.card-table {
    border-top: 1px solid #e9edf4;
}

.card-profile .card-header {
    height: 5rem;
    background-size: cover;
    border: 0;
}

.card-link+.card-link {
    margin-left: 1rem;
}

.card-body+.card-list-group {
    border-top: 1px solid #e9edf4;
}

.card-list-group .list-group-item {
    border-right: 0;
    border-left: 0;
    border-radius: 0;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    &:last-child {
        border-bottom: 0;
    }
    &:first-child {
        border-top: 0;
    }
}

.card-header-pills {
    margin: -.75rem 0;
    margin-right: -0.75rem;
    margin-left: -0.75rem;
}

.card-aside {
    -ms-flex-direction: row;
    flex-direction: row;
}

.card-aside-column {
    min-width: 5rem;
    width: 30%;
    -ms-flex: 0 0 30%;
    flex: 0 0 30%;
    background: no-repeat center/cover;
}

.card-value {
    font-size: 1rem;
    line-height: 3.4rem;
    height: 3.4rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
    font-weight: 400;
    i {
        vertical-align: middle;
        font-size: 30px;
    }
}

.card-chart-bg {
    height: 4rem;
    margin-top: -1rem;
    position: relative;
    z-index: 1;
    overflow: hidden;
}

.card-options {
    margin-left: auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-order: 100;
    order: 100;
    margin-right: -.5rem;
    color: #9aa0ac;
    -ms-flex-item-align: center;
    align-self: center;
    a:not(.btn) {
        margin-left: .5rem;
        color: #76839a;
        display: inline-block;
        min-width: 1rem;
        &:hover {
            text-decoration: none;
            color: #6e7687;
        }
        i {
            font-size: 1rem;
            vertical-align: middle;
        }
    }
    .dropdown-toggle:after {
        display: none;
    }
}

.stretch-card {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: stretch;
    align-items: stretch;
    -webkit-justify-content: stretch;
    justify-content: stretch;
    >.card {
        width: 100%;
        min-width: 100%;
    }
}

.card-img-absolute {
    position: absolute;
    top: 0;
    height: 100%;
    width: 100%;
    margin-left: -28px;
}

.card-image {
    position: absolute;
    top: 0;
    height: 100%;
    margin-left: -126px;
}


/*Card options*/

.card-collapsed {
    > :not(.card-header):not(.card-status) {
        display: none;
    }
    .card-options-collapse i:before {
        content: '\e92d';
    }
}

.card-fullscreen {
    .card-options-fullscreen i:before {
        content: '\e992';
    }
    .card-options-remove {
        display: none;
    }
}


/*Card maps*/

.card-map {
    height: 15rem;
    background: #e9ecef;
}

.card-map-placeholder {
    background: no-repeat center;
}


/**Card tabs*/

.card-tabs {
    display: -ms-flexbox;
    display: flex;
}

.card-tabs-bottom .card-tabs-item {
    border: 0;
    &.active {
        border-top-color: $white;
    }
}

.card-tabs-item {
    -ms-flex: 1 1 auto;
    flex: 1 1 auto;
    display: block;
    padding: 1.4rem 1.5rem;
    color: inherit;
    overflow: hidden;
}

a.card-tabs-item {
    background: #fafbfc;
    &:hover {
        text-decoration: none;
        color: inherit;
    }
    &:focus {
        z-index: 1;
    }
    &.active {
        background: $white;
        border-bottom-color: $white;
    }
}


/**Card status*/

.card-status {
    position: absolute;
    top: -1px;
    left: 0px;
    right: 0px;
    height: 3px;
    border-radius: 7px 7px 0 0;
}

.card-status-left {
    right: auto;
    bottom: 0;
    height: auto;
    width: 3px;
    border-radius: 3px 0 0 3px;
}


/**Card icon*/

.card-icon {
    width: 3rem;
    font-size: 2.5rem;
    line-height: 3rem;
    text-align: center;
}


/**Card fullscreen*/

.card-fullscreen {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 9999;
    margin: 0;
}


/**Card alert*/

.card-alert {
    border-radius: 0;
    margin: -1px -1px 0;
}

.card-category {
    font-size: 18px;
    text-transform: uppercase;
    text-align: center;
    font-weight: 500;
    letter-spacing: .05em;
    margin: 0 0 .5rem;
    background: #f6f6fb;
}

.item-card {
    .cardbody {
        position: relative;
        padding: 30px 15px;
        border-radius: 0 0 .2rem .2rem;
    }
    .cardtitle {
        span {
            display: block;
            font-size: .75rem;
        }
        a {
            color: #707070;
            font-weight: 500;
            text-decoration: none;
        }
    }
    .cardprice {
        position: absolute;
        top: 30px;
        right: 15px;
        span {
            &.type--strikethrough {
                opacity: .7;
                text-decoration: line-through;
            }
            display: block;
            color: #707070;
        }
    }
}

.card-padding {
    padding: 14px 2px 2px 11px;
}

.card-items {
    padding: 10px;
}

.card .box {
    .img img {
        width: 150px;
        height: 150px;
        border-radius: 100%;
    }
    h2 {
        font-size: 20px;
        color: #262626;
        margin: 20px auto;
        span {
            font-size: 14px;
            color: $white;
            display: inline-block;
            padding: 4px 10px;
            border-radius: 15px;
        }
    }
    p {
        color: #262626;
    }
}

.card-counter {
    padding: 20px 10px;
    background-color: $white;
    height: 100px;
    border-radius: 7px;
    transition: .3s linear all;
    margin-bottom: 1.5rem;
    &:hover {
        transition: .3s linear all;
    }
    &.primary {
        color: #FFF;
    }
    &.secondary {
        background: linear-gradient(to bottom right, #fbc434 0%, #f66b4e 100%) !important;
        color: #FFF;
    }
    &.success {
        background: linear-gradient(to bottom right, #62fb62, #21a544) !important;
        color: #FFF;
    }
    &.info {
        background: linear-gradient(to bottom right, #00f2fe 0%, #1e63c3 100%) !important;
        color: #FFF;
    }
    i {
        font-size: 4rem;
        opacity: 0.3;
    }
    .count-numbers {
        position: absolute;
        right: 35px;
        top: 10px;
        font-size: 32px;
        display: block;
    }
    .count-name {
        position: absolute;
        right: 35px;
        top: 65px;
        text-transform: capitalize;
        opacity: 0.5;
        display: block;
        font-size: 18px;
    }
}

.card-label {
