        border: 1px solid #d6d8db;
        background-color: #f6f6fb;
        background-size: .5rem;
    }
}

.custom-control-description {
    line-height: 1.5rem;
}

.form-range {
    -ms-flex-align: center;
    align-items: center;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background: none;
