.profile-user-online {
    background-repeat: no-repeat;
}

.slick-slider {
    position: relative;
    display: block;
    touch-action: pan-y;
    -webkit-tap-highlight-color: transparent;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
    transform: translate3d(0, 0, 0);
}

.slick-slider .slick-list {
    margin: 0 -6px;
}

.slick-list {
    position: relative;
    overflow: hidden;
    display: block;
    padding: 0;
}

.slick-track {
    position: relative;
    left: 0;
    top: 0;
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.slick-track:before,
.slick-track:after {
    content: '';
    display: table;
}

.slick-slider .slick-list .slick-slide {
    padding: 0 6px;
}

.slick-slide {
    float: left;
    height: 100%;
    min-height: 1px;
}

.recent-box .recent-profile {
    height: 90px;
    width: 100%;
    border-radius: 10px;
    position: relative;
}

.chat-profile {
    line-height: 1;
    color: $default-color;
    i {
        font-size: 14px;
    }
}

.chat-image {
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

.shared-files {
    padding: 12px;
    border-radius: 5px;
    font-size: 14px;
    border: 1px solid $border;
}

@media (min-width: 992px) {
    .main-content-app {
        height: calc(100vh - 250px);
    }
}

.main-chat-list .media:hover,
.main-chat-list .media:focus {
    background: $white !important;
}

